









































import {
    Component, Provide, Prop, Vue
} from 'vue-property-decorator';
import API from '@/api/api.js';
import { mapActions, mapGetters } from 'vuex';

interface FormObject {
    name: String,
    password: String
}

@Component({
    ...mapGetters(['categories']),
    ...mapActions(['getTopics', 'getCategories'])
})

export default class HomeTab extends Vue {
    
    @Provide() dialogVisible: Boolean = false;
    @Provide() form: FormObject = {
        name:'',
        password:''
    };

    created() {
        this.$store.dispatch('getCategories');
        this.$store.dispatch('getTopics', {id: 1});
    }

    goTabPage(id:number) {
        this.$store.dispatch('getTopics', {id});
        if (id === 1) {
            this.$router.push('/').catch(err => {
                console.log(err);
            });
        } else {
            this.$router.push(`/category/${id}`).catch(err => {
                console.log(err);
            });
        }
    }

    onSubmit() {
        console.log(this.form);
        
        API.login({
            name: this.form.name,
            password: this.form.password
        }).then((res:any) => {
            this.$message({
                message: '恭喜你，登录成功',
                type: 'success'
            });
            this.$store.commit('setUser', res);
            this.dialogVisible = false;

            // 存入缓存
            localStorage.setItem('user', JSON.stringify(res));
        }).catch(error => {
            this.$message.error('账号或密码有误');
            this.dialogVisible = false;
        });
    }
    

    get tabs() {
        return this.$store.state.categories;
    }

    get userMsg() {
        return this.$store.state.user;
    }
}
